.row {
    display: block;
    width: 100%;
}

.styled-label {
    font-size: 1.5rem;
    display: inline-block;
    width: 20%;
    text-align: right;
    padding-right: 10px;
}

.styled-input {
    border-radius: 3px;
    margin: 0 0 1em;
    width: 70%;
    padding: 5px;
    ::placeholder {
        font-size: 0.7rem;
    }
}